import {
  FC,
  createContext,
  PropsWithChildren,
  useContext,
  useMemo,
} from "react";
import { AuthProviderType } from "./types";
import { authProviderInitialValues } from "./constants";
import { useBooleanState } from "../../app/hooks/use-boolean-state";

export const AuthContext = createContext<AuthProviderType>(
  authProviderInitialValues
);

export const useAuth = () => useContext(AuthContext);

export const AuthProvider: FC<PropsWithChildren> = ({ children }) => {
  const authState = useBooleanState();

  const authProviderValue = useMemo<AuthProviderType>(() => {
    return {
      accessToken: "",
      login: authState.turnOn,
      logout: authState.turnOff,
      isAuthorized: authState.isOn,
    };
  }, [authState.isOn]);

  return <AuthContext.Provider value={authProviderValue}>{children}</AuthContext.Provider>;
};
