import { TextField, InputAdornment } from "@mui/material";
import SearchRoundedIcon from "@mui/icons-material/SearchRounded";
import { searchFieldWrapperSx, searchInputSx } from "./search-field.styles";
import { ChangeEventHandler, FC, useCallback } from "react";
import { debounce } from "lodash";

interface Props {
  onDebounced: (value: string) => void;
}

export const SearchField: FC<Props> = ({ onDebounced }) => {
  const onChange = useCallback(
    debounce<ChangeEventHandler<HTMLInputElement>>((event) => {
      onDebounced(event.target.value);
    }, 500),
    []
  );

  return (
    <TextField
      sx={searchFieldWrapperSx}
      placeholder="Search"
      onChange={onChange}
      InputProps={{
        sx: searchInputSx,
        startAdornment: (
          <InputAdornment position="start">
            <SearchRoundedIcon />
          </InputAdornment>
        ),
      }}
    />
  );
};
