import { Components, Theme } from "@mui/material";

import fontPath from "../../../fonts/DMSans-Regular.ttf";

export const MuiCssBaseline: Components<
  Omit<Theme, "components">
>["MuiCssBaseline"] = {
  styleOverrides: `
        @font-face {
          font-family: 'DM Sans';
          font-style: normal;
          font-weight: 400;
          src: local('DM Sans'), local('DM Sans'), url({${fontPath}}) format('ttf');
        }
      `,
};
