import AvatarMui from "@mui/material/Avatar";
import { useDropDownAnchor } from "../../../../app/hooks";
import { DropDown } from "../../../drop-down/drop-down.component";
import { useAuth } from "../../../../features/auth";
import { useMemo } from "react";
import { DropDownOption } from "../../../../app/types";
import { IconButton } from "@mui/material";

export const Avatar = () => {
  const { handleRemoveAnchorEl, handleSetAnchorEl, anchorEl, hasAnchorEl } =
    useDropDownAnchor();

  const { logout } = useAuth();

  const options = useMemo((): DropDownOption[] => {
    return [{ label: "Log out", onClick: logout }];
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div>
      <IconButton onClick={handleSetAnchorEl}>
        <AvatarMui>A</AvatarMui>
      </IconButton>

      <DropDown
        onClose={handleRemoveAnchorEl}
        isOpen={hasAnchorEl}
        anchorEl={anchorEl}
        options={options}
      />
    </div>
  );
};
