import { createContext, PropsWithChildren, FC } from "react";
import { useGetUsersMetadataQuery } from "../../app/data-access/dashboards";
import { Loader } from "../../components";

const MetadataContext = createContext(null);

export const MetadataProvider: FC<PropsWithChildren> = ({ children }) => {
  const { data, isLoading, isInitialLoading, isFetching } =
    useGetUsersMetadataQuery();

  if (isLoading || isInitialLoading || isFetching) {
    return  <Loader centered />
  }

  return (
    <MetadataContext.Provider value={null}>{children}</MetadataContext.Provider>
  );
};
