import { Box, Button, Typography } from "@mui/material";
import { signInSubtitleSx, signInTitleSx } from "./sign-in.styles";
import { TextField } from "../text-field";
import { useAuth } from "../../../auth-provider.component";
import { useNavigate } from "react-router-dom";

export const SignIn = () => {
  const { login } = useAuth();
  const navigate = useNavigate();

  const onSubmit = () => {
    login();
    navigate("/dashboard", { replace: true });
  };

  return (
    <Box>
      <Typography sx={signInTitleSx}>Sign In</Typography>
      <Typography sx={signInSubtitleSx} variant="caption">
        Enter your email and password to sign in!
      </Typography>

      <TextField label="Email*" name="email" />
      <TextField label="Password*" name="password" />
      <Button
        variant="contained"
        fullWidth
        sx={{ mt: 4, height: 54, textTransform: "none" }}
        onClick={onSubmit}
      >
        Sign In
      </Button>
    </Box>
  );
};
