import {
  Box,
  List,
  ListItem,
  Typography,
  ListItemIcon,
  ListItemText,
  ListItemButton,
  IconButton,
} from "@mui/material";
import {
  getListItemSx,
  getSidenavWrapperSx,
  getSidenavCollapseButtonSx,
} from "./sidenav.styles";
import { useLocation, useNavigate } from "react-router-dom";
import { categoriesList } from "./constants";
import { useSidenavControl } from "./hooks/use-sidenav-control";
import KeyboardArrowLeftRoundedIcon from "@mui/icons-material/KeyboardArrowRightRounded";

export const Sidenav = () => {
  const { hoverState, collapseState } = useSidenavControl();

  const navigate = useNavigate();
  const location = useLocation();

  const activeCategory = location.pathname;

  const onClick = (link: string) => () => {
    navigate(link);
  };

  return (
    <Box
      sx={getSidenavWrapperSx(hoverState.isOn, collapseState.isOn)}
      onMouseLeave={hoverState.turnOff}
      onMouseOver={hoverState.turnOn}
    >
      <Box overflow="hidden">
        <Box
          display="flex"
          alignItems="flex-end"
          gap={2}
          my={2}
          px={2}
          position="relative"
        >
          <img src="logo.svg" width="30" height="40" alt="dropsLook logo" />
          <Typography variant="h5">DropsLook</Typography>
        </Box>

        <List>
          {categoriesList.map(({ primary, icon: Icon, link }) => (
            <ListItem
              key={primary}
              disablePadding
              sx={getListItemSx(activeCategory === link)}
            >
              <ListItemButton onClick={onClick(link)}>
                <ListItemIcon>
                  <Icon />
                </ListItemIcon>

                <ListItemText primary={primary} />
              </ListItemButton>
            </ListItem>
          ))}
        </List>
      </Box>
    
      {hoverState.isOn && (
        <IconButton
          sx={getSidenavCollapseButtonSx(collapseState.isOn)}
          onClick={collapseState.toggle}
        >
          <KeyboardArrowLeftRoundedIcon />
        </IconButton>
      )}
    </Box>
  );
};
