import {
  styled,
  Breadcrumbs,
  Box,
  IconButton,
  Typography,
} from "@mui/material";
import { Link, useLocation } from "react-router-dom";
import { useMemo } from "react";
import { headerDashboardTitleSx, headerUserWrapperSx } from "./header.styles";

import NotificationsNoneIcon from "@mui/icons-material/NotificationsNone";
import DarkModeIcon from "@mui/icons-material/DarkMode";
import HelpOutlineIcon from "@mui/icons-material/HelpOutline";
import { SearchField } from "./components/search-field/search-field.component";
import { Avatar } from "./components/avatar/avatar.component";
import { headerLocationMap } from "./constants";
import upperFirst from "lodash/upperFirst";

const StyledHeader = styled("header")`
  justify-content: space-between;
  align-items: center;
  grid-column: 2/3;
  display: flex;
`;

const StyledLink = styled(Link)`
  text-decoration: none;
  color: "#707EAE";
`;

export const Header = () => {
  const { pathname } = useLocation();

  const breadCramps = useMemo(() => {
    const paths = pathname.slice(1).split("/");

    return paths.map((path, index) => {
      const link = index === 1 ? pathname : `/${path}`;

      return (
        <StyledLink key={path} to={link}>
          {upperFirst(path)}
        </StyledLink>
      );
    });
  }, [pathname]);

  return (
    <StyledHeader>
      <Box>
        <Breadcrumbs separator="/" aria-label="breadcrumb">
          {breadCramps}
        </Breadcrumbs>

        <Typography variant="h4" sx={headerDashboardTitleSx}>
          {headerLocationMap[pathname as keyof typeof headerLocationMap]}
        </Typography>
      </Box>

      <Box sx={headerUserWrapperSx}>
        <SearchField />

        <Box display="flex" gap={1}>
          <IconButton>
            <NotificationsNoneIcon />
          </IconButton>

          <IconButton>
            <DarkModeIcon />
          </IconButton>

          <IconButton>
            <HelpOutlineIcon />
          </IconButton>
        </Box>

        <Avatar />
      </Box>
    </StyledHeader>
  );
};
