import { useMutation, useQuery } from "@tanstack/react-query";
import { UserMetadata, UserResponse } from "./types";
import { ApiInstance } from "../../../axios";

export const useGetUsersQuery = () => {
  return useQuery({
    queryKey: ["dashboard-users-data"],
    queryFn: () => ApiInstance.get<UserResponse>("adviserInterest"),
  });
};

export const useUpdateUserMutation = () => {
  return useMutation({
    mutationFn: () => ApiInstance.get<UserResponse>("adviserInterest"),
  });
};

export const useGetUsersMetadataQuery = () => {
  return useQuery({
    queryKey: ["dashboard-users-metadata"],
    queryFn: () =>
      ApiInstance.get<UserMetadata>("adviserInterest", {
        params: {
          onlyMetadata: true,
        },
      }),
  });
};
