import { Components, Theme } from "@mui/material";

export const MuiTextField: Components<
  Omit<Theme, "components">
>["MuiTextField"] = {
  styleOverrides: {
    root: {
      width: "100%",
      borderColor: "#E0E5F2",

      ".MuiOutlinedInput-input:focus": {
        outline: "none",
        border: "none",
        boxShadow: "none",

        "& ~ fieldset": { borderColor: "inherit" },
      },
    },
  },
};
