import { useState, useMemo } from "react";
import { useQuery } from "@tanstack/react-query";
import { ApiInstance } from "../axios";

interface PaginationResponse<T> {
  error: null | string;
  totalSize: number;
  data: T;
  firstId: string;
  lastId: string;
}

export interface SortingState {
  orderBy: string;
  isOrderAsc: boolean;
}

export interface FilterState {
  filteredValue: string;
  filteredBy: string;
  conditional: "<" | ">" | "==";
}

export const usePaginationAndSorting = <T>(url: string) => {
  const [size, setSize] = useState(10);
  const [page, setPage] = useState<Record<string, string>>({});

  const [sortingState, setOrderBy] = useState<SortingState>();
  const [filterState, setFilterState] = useState<FilterState>();

  const { data, isLoading, isFetching, isInitialLoading } = useQuery<
    PaginationResponse<T>
  >({
    queryKey: [
      `pagination-request-${url}-${size}-${JSON.stringify(
        page
      )}-${JSON.stringify(sortingState)}-${JSON.stringify(filterState)}`,
    ],
    queryFn: () =>
      ApiInstance.get(url, {
        params: {
          limit: size,
          ...page,
          ...sortingState,
          ...filterState,
        },
      }),
  });

  return useMemo(
    () => ({
      isLoading: isLoading || isFetching,
      handleChangePaginationSize: setSize,
      handleChangePaginationPage: setPage,
      isError: Boolean(data?.error),
      isInitialLoading,
      currentPage: page,
      setFilterState,
      pageSize: size,
      sortingState,
      setOrderBy,
      data,
    }),
    [
      isLoading,
      setOrderBy,
      isFetching,
      sortingState,
      isInitialLoading,
      data,
      page,
      size,
    ]
  );
};
