import {
  TableRow,
  TableBody,
  TableCell,
  TableHead,
  Typography,
  Table as TableMui,
} from "@mui/material";
import { FC } from "react";
import { TableConfig } from "./types";
import { tableHeadCellSx, tableRowSx } from "./table.styles";
import { SortingLabel } from "./components/sorting-label.component";
import { SortingState } from "../../app/hooks/use-pagination";
import { useCallback } from "react";
import { Loader } from "../loader/loader.component";

interface Props {
  config: TableConfig;
  isLoading: boolean;
  onClickRow?: any;
  sortingState?: SortingState;
  handleChangeSorting: (state: SortingState) => void;
}

export const Table: FC<Props> = ({
  handleChangeSorting,
  sortingState,
  isLoading,
  config,
}) => {
  const { tableHead, tableBody } = config;

  const onHeadCellClick = useCallback(
    (key: string, isOrderAsc?: boolean) => () => {
      handleChangeSorting({
        isOrderAsc: !Boolean(isOrderAsc),
        orderBy: key,
      });
    },
    [handleChangeSorting]
  );

  return (
    <TableMui>
      <TableHead>
        <TableRow>
          {tableHead.map(({ primary, key, width }, index) => (
            <TableCell
              onClick={onHeadCellClick(key, sortingState?.isOrderAsc)}
              sx={tableHeadCellSx}
              width={width}
              key={index}
            >
              {primary}

              <SortingLabel
                isAscending={
                  key === sortingState?.orderBy
                    ? sortingState?.isOrderAsc
                    : undefined
                }
              />
            </TableCell>
          ))}
        </TableRow>
      </TableHead>

      {isLoading ? (
        <Loader centered size="30px" />
      ) : (
        <TableBody>
          {!tableBody.length && (
            <Typography display="block" textAlign="center">
              No Data
            </Typography>
          )}

          {tableBody.map(([id, data]) => {
            return (
              <TableRow key={id} sx={tableRowSx}>
                {Object.entries(tableHead).map(([_, { key }]) => (
                  <TableCell key={key}>{data[key]}</TableCell>
                ))}
              </TableRow>
            );
          })}
        </TableBody>
      )}
    </TableMui>
  );
};
