import { useState, useMemo, useCallback } from "react";

export const useDropDownAnchor = () => {
  const [anchorEl, setAnchorEl] = useState<null | HTMLElement>(null);
  const hasAnchorEl = Boolean(anchorEl);

  const handleSetAnchorEl = useCallback(
    (event: React.MouseEvent<HTMLButtonElement>) => {
      console.log("set");
      setAnchorEl(event.currentTarget);
    },
    []
  );

  const handleRemoveAnchorEl = useCallback(() => {
    console.log("remove");
    setAnchorEl(null);
  }, []);

  return useMemo(() => {
    return { handleSetAnchorEl, handleRemoveAnchorEl, hasAnchorEl, anchorEl };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [hasAnchorEl, anchorEl]);
};
