import { SwipeableDrawer } from "@mui/material";
import { FC } from "react";

interface Props {
  onClose: VoidFunction;
  isOpen: boolean;
}

export const Drawer: FC<Props> = ({ isOpen, onClose }) => {
  return (
    <SwipeableDrawer
      onOpen={() => {}}
      open={isOpen}
      onClose={onClose}
      slotProps={{ backdrop: { sx: { background: "transparent" } } }}
      anchor="right"
    >
      drawer
    </SwipeableDrawer>
  );
};
