import { Theme } from "@mui/material";

export const getSidenavWrapperSx = (
  isHovered: boolean,
  isCollapsed: boolean
) => ({
  boxShadow: isHovered ? 1 : "none",
  backgroundColor: "common.white",
  transition: "all .2s",
  position: "relative",
  gridColumn: "1/2",
  width: isCollapsed ? "60px" : "300px",
});

export const getListItemSx = (isActive: boolean) => ({
  fontFamily: "DM Sans",
  fontWeight: 400,
  color: isActive ? "primary.main" : "#A3AED0",

  svg: {
    fill: ({ palette }: Theme) => (isActive ? palette.primary.main : "#A3AED0"),
  },
});

export const getSidenavCollapseButtonSx = (isCollapsed: boolean) => ({
  transform: `translateX(50%) translateY(-50%) ${
    isCollapsed ? "" : "rotate(180deg)"
  }`,
  transition: "all .2s",
  backgroundColor: "common.white",
  position: "absolute",
  border: '1px solid',
  borderColor: 'grey.300',
  zIndex: 2,
  top: "50%",
  right: "0",
});
