import { Box } from "@mui/material";
import {
  leftSideContentSx,
  leftSideWrapperSx,
  loginRightSideSx,
  loginWrapperSx,
} from "./login.styles";
import { SignIn } from "./components/sign-in/sign-in.component";

export const Login = () => {
  return (
    <Box sx={loginWrapperSx}>
      <Box sx={leftSideWrapperSx}>
        <Box sx={leftSideContentSx}>
          <SignIn />
        </Box>
      </Box>

      <Box sx={loginRightSideSx}>
        <img src="logo.svg" width="250" height="200" alt='dropSlook logo'/>
      </Box>
    </Box>
  );
};
