import { FC } from "react";
import { CircularProgress, Box } from "@mui/material";
import { centeredLoaderSx } from "./loader.styles";

interface Props {
  centered?: boolean;
  size?: string;
}

export const Loader: FC<Props> = ({ centered = false, size = "50px" }) => {
  return (
    <Box sx={[centered && centeredLoaderSx]}>
      <CircularProgress size={size} />
    </Box>
  );
};
