import { useMemo } from "react";
import { useBooleanState } from "../../../app/hooks";

export const useSidenavControl = () => {
  const collapseState = useBooleanState();
  const hoverState = useBooleanState();

  return useMemo(
    () => ({ collapseState, hoverState }),
    [collapseState, hoverState]
  );
};
