import {
  TablePagination,
  Button,
  IconButton,
  Stack,
  Box,
  Typography,
} from "@mui/material";
import { usersWrapperSx } from "./users.styles";
import { Drawer, SearchField, Table } from "../../components";
import { useMemo, useState } from "react";

import { TableConfig } from "../../components/table/types";
import { tableHeadConfig } from "./constants";
import { useBooleanState } from "../../app/hooks";
import { usePaginationAndSorting } from "../../app/hooks/use-pagination";
import FilterListRoundedIcon from "@mui/icons-material/FilterListRounded";
import AddCircleOutlineRoundedIcon from "@mui/icons-material/AddCircleOutlineRounded";

export const Users = () => {
  const {
    data,
    pageSize,
    setOrderBy,
    isLoading,
    isInitialLoading,
    sortingState,
    setFilterState,
    handleChangePaginationPage,
    handleChangePaginationSize,
  } = usePaginationAndSorting("adviserInterest");

  const [page, setPage] = useState(0);

  const modalState = useBooleanState();
  const drawerState = useBooleanState();

  const handleChangePage = (
    event: React.MouseEvent<HTMLButtonElement> | null,
    newPage: number
  ) => {
    if (data) {
      handleChangePaginationPage(
        newPage > page
          ? { startAfter: data.lastId }
          : { endBefore: data.firstId }
      );

      setPage(newPage);
    }
  };

  const tableConfig = useMemo((): TableConfig => {
    if (!data?.data) {
      return {
        tableHead: tableHeadConfig,
        tableBody: [],
      };
    }

    const tableBodyRows = Object.entries(data?.data);

    return {
      tableHead: tableHeadConfig,
      tableBody: tableBodyRows,
    };
  }, [data]);

  return (
    <Box sx={usersWrapperSx}>
      <Box
        sx={{
          display: "grid",
          gridTemplateColumns: "50px minmax(200px,500px) auto",
          gap: 1,
        }}
      >
        <IconButton onClick={drawerState.turnOn}>
          <FilterListRoundedIcon />
        </IconButton>

        <SearchField
          onDebounced={(value) => {
            console.log(value);
            if (!value) {
              setFilterState(undefined);
            } else {
              setFilterState({
                filteredBy: "firstName",
                filteredValue: value,
                conditional: "==",
              });
            }
          }}
        />

        <Button variant="contained" sx={{display: 'flex', alignItems: 'center', gap: 1, justifySelf: "end" }}>
          <AddCircleOutlineRoundedIcon />
          <Typography variant="caption" lineHeight='100%'>Add user</Typography>
        </Button>
      </Box>

      <Box flexGrow={1} position="relative">
        <Table
          config={tableConfig}
          sortingState={sortingState}
          onClickRow={modalState.turnOn}
          handleChangeSorting={setOrderBy}
          isLoading={isLoading || isInitialLoading}
        />
      </Box>

      {data && !isLoading && (
        <Stack spacing={4}>
          <TablePagination
            component="div"
            count={data.totalSize}
            page={page}
            onPageChange={handleChangePage}
            rowsPerPage={pageSize}
            onRowsPerPageChange={(event) =>
              handleChangePaginationSize(Number(event.target.value))
            }
          />
        </Stack>
      )}

      <Drawer isOpen={drawerState.isOn} onClose={drawerState.turnOff} />
    </Box>
  );
};
